export default {
  props: [],
  data() {
    return {
      commentInputText: "",
      getDetails: false
    };
  },
  mounted() {
    console.log(this.commentInputText.length);
  },
  methods: {
    keyUpEvent(refCode) {
      let entry = this.$refs[refCode];
      this.commentInputText = entry.value;
      console.log(this.commentInputText.length);
    },
    getCredentials() {
      this.getDetails = true;
    }
  }
};
